<template>
  <v-container class="fill-height" fluid>
    <v-row align="center" justify="center">
      <v-col cols="12" sm="8" md="4">
        <v-card class="elevation-12" >
          <v-form @submit.prevent="login">
            <v-toolbar color="primary" dark flat>
              <v-toolbar-title
                >ETriage Dashboard Login</v-toolbar-title
              >
              <v-spacer></v-spacer>
            </v-toolbar>
            <v-card-text>
              <v-text-field 
                label="Employee ID Number"
                name="login"
                prepend-icon="mdi-account"
                type="text"
                autofocus
                autocomplete="off"
                v-model="loginForm.code"
                required
              ></v-text-field>
              <v-text-field
                id="password"
                label="Password"
                name="password"
                prepend-icon="mdi-lock"
                type="password"
                v-model="loginForm.password"
                required
              ></v-text-field>
            </v-card-text>
            <v-container>
              <v-alert type="error" class="text-center" v-if="formMessage != null && formMessage != 'Please wait...'">
                {{ formMessage }}
              </v-alert>
            </v-container>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn type="submit" color="primary" v-if="!isLoginLoading"
                >Login</v-btn
              >
              <v-progress-circular v-if="isLoginLoading" class="mx-3" 
                indeterminate
                color="primary"
              ></v-progress-circular>
              <v-btn text to="/">Cancel</v-btn>
            </v-card-actions>
          </v-form>
        </v-card>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import { mapGetters } from "vuex";
export default {
  data: () => ({
    isLoginLoading: false,
    loginForm: {
      code: null,
      password: null,
    },
  }),
  computed: {
    ...mapGetters(["formMessage","isLoggedIn"]),
  },
  mounted() {
    if (this.isLoggedIn) {
      this.$router.push("/main");
    }
  },
  methods: {
    async login() {
      this.isLoginLoading = true;
      await this.$store.dispatch("loginEmployee", this.loginForm);
      this.isLoginLoading = false;
      if (this.formMessage == null) {
        this.$router.push("/main");
      }
    },
  },
};
</script>

<style scoped></style>